import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';

import { LoadingService } from 'src/app/shared/services/loading.service';
import { State } from './state.enum.model';
import { Todo } from './todo.model';
import { TodoFormService } from './todo-form.service';
import { TodoService } from './todo.service';


@Injectable({
  providedIn: 'root'
})
export class StateService {

  constructor(
    private todo: TodoService,
    private todoForm: TodoFormService,
    private loading: LoadingService
  ) { }

  doing() {
    return this.todoForm.form.valid
      ? this.todo.post({
        name: this.todoForm.form.get('name').value,
        description: this.todoForm.form.get('description').value,
        state: State.DOING
      }).pipe(tap(() => this.todoForm.form.reset()))
      : this.todo.get();
  }

  done(todo: Todo): Observable<Todo[]> {
    if (State.DONE === todo.state) {
      return this.todo.delete(todo);
    }
    todo.state = State.DONE;
    this.loading.start();
    return this.todo.put(this.todo.todoList).pipe(tap(
      () => this.loading.stop(),
      () => todo.state = State.DOING,
    ));
  }

}
